import React, { useContext, useState } from "react";
import { useAuthState } from "react-firebase-hooks/auth";
import { SmileContext } from "../context";
import { useCollectionData } from "react-firebase-hooks/firestore";
import { collection, addDoc } from "firebase/firestore";
import Loader from "./Loader";

const Chat = () => {
  const { auth, db } = useContext(SmileContext);
  const [user] = useAuthState(auth);

  const [messages, loading] = useCollectionData(collection(db, "messages"));

  if (loading === false) {
    messages.sort((a, b) => parseFloat(a.createdAt) - parseFloat(b.createdAt));
  }

  const [value, setValue] = useState("");
  const sendMessage = async () => {
    addDoc(collection(db, "messages"), {
      uid: user.uid,
      displayName: user.displayName,
      photoURL: user.photoURL,
      text: value,
      createdAt: Date.now(),
    });
    setValue("");
  };

  if (loading) {
    return <Loader />;
  }

  const onKeyDown = (e) => {
    if (e.keyCode == 13) {
      sendMessage();
    }
  };

 
  return (
    <div
    style={{
      width: "100%",
      height: "100%",
      overflowY: "auto",
      background: 'rgb(14, 22, 33)',

    }}
    
    >
      <div
      style={{margin: '50px'}}
      >

      <div
        style={{

          height: "75vh",
          borderBottom: "1px solid gray",
          overflowY: "auto",
          margin: "0 auto",
          background: 'rgb(14, 22, 33)',
        }}
      >
        
        {messages.map((message) => (
          <div
            style={{
              color: 'white',
              margin: "10px",
              background:
                user.uid === message.uid ? "rgb(43, 83, 120)" : "rgb(24, 37, 51)",
              marginLeft: user.uid === message.uid ? "auto" : "10px",
              width: "fit-content",
              padding: "15px",
              borderRadius: '15px'
            }}
            className="message"
          >
            <div className="messageInside">
              <img src={message.photoURL} alt="" />
              <p>{message.displayName}</p>
            </div>
            <p
            style={{
              wordBreak: 'break-word'
            }}
            
            >{message.text}</p>
          </div>
        ))}
      </div>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          margin: "0 auto",
          alignItems: "flex-end",
          border: 'none'
        }}
      >
        <textarea
          onKeyDown={onKeyDown}
          value={value}
          onChange={(event) => setValue(event.target.value)}
          placeholder='Input text...'
          rows="3"
          style={{
            resize: 'none',
            width: "100%",
            height: '100%',
            background: 'none',
            color: 'white',
            fontSize: '18px',
            alignSelf: 'center',
            wordBreak: 'break-all',
            border: 'none',
            padding: '5px',
            outline: 'none'
          }}
        ></textarea>

      </div>
          
      </div>
      
    </div>
  );
};

export default Chat;
