import { Box, Button, Grid } from '@mui/material'
import { Container } from '@mui/system'
import React, { useContext } from 'react'
import { SmileContext } from '../context'
import { GoogleAuthProvider, signInWithPopup } from "firebase/auth";

// import { Context123 } 

const Login = () => {
    // const { auth } = useContext(Context123)
    // const { auth } = useContext(Context123)
    // const { auth } = useContext(Context)
    const { auth } = useContext(SmileContext)

    const login = async () =>{
        const provider = new GoogleAuthProvider();
        // const {user} = auth.signWithPopup(provider)
        // signInWithPopup(auth, provider)
        const user = await signInWithPopup(auth, provider)
        console.log(user)

    }


    return (
        <Container>
            <Grid container 
                style={{height: '80vh'}}
                justifyContent="center"
                alignItems="center"
            
            >
                <Grid container
                justifyContent="center"
                // alignItems="center"
                style={{width: '400px', background: 'rgb(30, 30, 30)'}}>
                    <Box p={5}>
                        <Button 
                        onClick={login}
                        
                        variant="contained">Войти с помощью гугл</Button>


                    </Box>


                </Grid>

            </Grid>
        </Container>
    )
}

export default Login